<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>Source: {{blog.author}}</p>
		<p>Categories:</p>
		<ul>
			<li v-for="category in blog.categories">
				{{category}}
			</li>
		</ul>
		<button @click="deleteSingleBlog()">delete</button>
		<router-link :to="'/edit/'+id">edit</router-link>
	</div>
</template>

<script>
	import axios from'axios'
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			axios.get('https://blog-demo-a1bf5-default-rtdb.firebaseio.com/posts/'+this.id+".json")
			.then((data) =>{
				this.blog=data.data;

				})
		},
		methods:{
			deleteSingleBlog(){
				axios.delete('https://blog-demo-a1bf5-default-rtdb.firebaseio.com/posts/'+this.id+".json")
				.then(response =>{
					this.$router.push({path:'/'})
					})
				}
		}
	}
</script>

<style>
	#single-blog{
		max-width:960px;
		margin:0 auto;
		padding:20px;
		background:#eee;
		border:1px dotted #aaa;
	}
</style>
