<template>
	<div id="add-blog">
		<h2>Edit Blog</h2>
		<form v-if="!submmited">
			<label>Blog Title:</label>
			<input type="text" v-model="blog.title"required/>
			
			<label>Blog Content:</label>
			<textarea v-model="blog.content"></textarea>
			<div id="checkboxes">
				<label>Fashion</label>
				<input type="checkbox" value="Fashion" v-model="blog.categories">
				<label>Travel</label>
				<input type="checkbox" value="Travel" v-model="blog.categories">
				<label>Sports</label>
				<input type="checkbox" value="Sports" v-model="blog.categories">
				<label>Music</label>
				<input type="checkbox" value="Music" v-model="blog.categories">
				
			</div>
			<label>Source:</label>
			<select v-model="blog.author">
				<option v-for="author in authors">
				{{author}}
				</option>
			</select>
			<button v-on:click.prevent="post">Submit</button>
			
		</form>
		<div v-if="submmited">
			<h3>
				Submitted!
			</h3>
		</div>

		
		
	</div>
</template>

<script>
	import axios from'axios'
	export default{
		name:'add-blog',
		data(){
			return{
				id:this.$route.params.id,
				blog:{},
				submmited:false,
				authors:["Website","Social Media","Original"]
				}
		},
		methods:{
			fetchData(){
				axios.get('https://blog-demo-a1bf5-default-rtdb.firebaseio.com/posts/'+this.id+".json")
				.then(response =>{
					
					this.blog=response.data;
					})
			},
			post:function(){
				axios.put('https://blog-demo-a1bf5-default-rtdb.firebaseio.com/posts/'+this.id+".json",this.blog)
				.then((data) =>{
					console.log(data.data);
					this.submmited =true;
				});
			}
		},
		created(){
			this.fetchData();
		}
	}
</script>

